import { parseISO } from 'date-fns'

const queryString = require('query-string');

export const withDirection = arr => ["SE", "S", "SW", "E", "", "W", "NE", "N", "NW"]
.map((e, i) => ({
    ...arr[i],
    direction: e
}))

const isBrowser = typeof window !== `undefined`

export const getQueryString = () => isBrowser && queryString.parse(window && window.location.search)

export const assign = url => isBrowser && window.location.assign(url)

export const parseStringDate = (str, fallback) => isNaN(parseISO(str).getTime()) ? fallback :  parseISO(str)